.header{
	background: #fff;
	padding: 12px 0 17px;
	@media (max-width: 991px){
		text-align: center;
	}
	@nest &__btn{
		width: 20px;
		height: 12px;
		padding: 0;
		border: 0;
		float: left;
		outline: none;
		margin-top: 29px;
		background: transparent;
		@media (min-width: 991px){
			display: none;
		}
		@nest &:hover &{
			@nest &-line{
				background: #002acc;
			}
		}
		@nest &-open &{
			@nest &-line{
				background: #002acc;
			}			
		}
		@nest &-line{
			display: block;
			height: 2px;
			width: 100%;
			background: #000;
			margin-bottom: 3px;
			transition: background 300ms;
			@nest &--md{
				width: 16px;
			}
			@nest &--sm{
				width: 13px;
				margin-bottom: 0;
			}
		}
	}
	@nest &__logo{
		margin: 0 0 0 44px;
		display: inline-block;
		vertical-align: middle;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 1199px){
			margin-left: 15px;
		}
		@media (max-width: 991px){
			margin: 0;
		}
	}
	@nest &__social{
		float: right;
		margin: 28px 0 0;
		max-width: 120px;
		@nest &-link{
			font-size: 16px;
			color: #0b0b17;
			margin: 0 0 0 18px;
			display: inline-block;
			vertical-align: middle;
			transition: color 300ms;
			@nest &:first-child{
				font-size: 14px;
			}
			@nest &:hover{
				color: #ff2a00;
			}
			@media (max-width: 1199px){
				margin-left: 15px;
			}
			@media (max-width: 512px){
				margin: 0 10px;
			}
		}
		@media (max-width: 512px){
			float: none;
			margin: 15px auto 0;
		}
	}
}