.comments-top{
	margin-bottom: 20px;
	@media (max-width: 767px){
		text-align: center;
	}
	@nest & h2{
		font-size: 45px;
		color: #0b0b17;
		font-weight: 800;
		margin: 0;
		@media (max-width: 767px){
			font-size: 25px;
		}
	}
	@nest &__btn{
		float: right;
		margin-top: 9px;
		@media (max-width: 767px){
			float: none;
			margin-top: 15px;
		}
	}
}