@import "normalize-css/normalize.css";
@import "slick-carousel/slick/slick.css";
@import "fancybox/source/jquery.fancybox.css";
@import "font-awesome.min.css";
/*! main */
@font-face {
  font-family: 'gilroy';
  src: url('../fonts/radomir_tinkov_-_gilroy-extrabold.woff2') format('woff2'),
       url('../fonts/radomir_tinkov_-_gilroy-extrabold.woff') format('woff'),
       url('../fonts/radomir_tinkov_-_gilroy-extrabold.ttf') format('truetype'),
       url('../fonts/radomir_tinkov_-_gilroy-extrabold.svg#gilroyextrabold') format('svg');
  font-weight: 800;
  font-style: normal;
}
@font-face {
  font-family: 'gilroy';
  src: url('../fonts/radomir_tinkov_-_gilroy-light.woff2') format('woff2'),
       url('../fonts/radomir_tinkov_-_gilroy-light.woff') format('woff'),
       url('../fonts/radomir_tinkov_-_gilroy-light.ttf') format('truetype'),
       url('../fonts/radomir_tinkov_-_gilroy-light.svg#gilroylight') format('svg');
  font-weight: 300;
  font-style: normal;
}

html, body{
	font-family: 'gilroy';
	font-weight: 300;
  background: #f6f6f6;
}