.news-link{
	font-size: 0;
	display: block;
	position: relative;
	overflow: hidden;
	text-decoration: none;
	margin-bottom: 32px;
	@media (max-width: 512px){
		max-height: 200px;
	}
	@nest &:hover{
		@nest &:after{
			opacity: 0;
		}
	}
	@nest &:after{
		position: absolute;
		content: '';
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		transition: opacity 300ms;
	}
	@nest &--lg &{
		@nest &__content{
			max-width: 650px;
			padding: 0 10px 87px 65px;
			@media (max-width: 1199px){
				padding: 0 10px 40px 30px;
			}
			@media (max-width: 767px){
				padding: 0 10px 30px 20px;
			}
			@media (max-width: 512px){
				padding: 0 10px 15px 15px;
			}
		}
	}
	@nest &--lg&{
		@nest &:after{
			background: color(#0b0b17 alpha(29%));
		}
		@nest & h2{
			font-size: 46px;
			line-height: 73px;
			margin: 20px 0 0;
			@nest & i{
				font-weight: 300;
				font-style: normal;
			}
			@media (max-width: 1199px){
				font-size: 40px;
				line-height: 60px;
			}
			@media (max-width: 991px){
				font-size: 35px;
				line-height: 40px;
			}
			@media (max-width: 767px){
				font-size: 30px;
				line-height: 35px;
			}
			@media (max-width: 512px){
				font-size: 17px;
				line-height: 23px;
				margin-top: 15px;
			}
			@media (max-width: 400px){
				font-size: 15px;
				line-height: 20px;
			}
		}
		@nest & p{
			font-size: 18px;
			line-height: 28px;
			margin: 17px 0 0;
			@media (max-width: 991px){
				font-size: 16px;
			}
			@media (max-width: 767px){
				margin-top: 10px;
				line-height: normal;
			}
			@media (max-width: 512px){
				font-size: 12px;
			}
		}
	}
	@nest &--md &{
		@nest &__content{
			max-width: 650px;
			padding: 0 10px 42px 42px;
			@media (max-width: 1199px){
				padding: 0 10px 25px 35px;
			}
			@media (max-width: 991px){
				padding: 0 10px 15px 20px;
			}
			@media (max-width: 512px){
				padding: 0 10px 15px 15px;
			}
		}
	}
	@nest &--md&{
		min-height: 331px;
		@nest &:after{
			background: color(#06060d alpha(56%));
		}
		@media (max-width: 512px){
			min-height: 1px;
		}
		@nest & h2{
			font-size: 30px;
			line-height: 36px;
			margin: 18px 0 0;
			@media (max-width: 1199px){
				font-size: 25px;
				line-height: 30px;
			}
			@media (max-width: 991px){
				font-size: 20px;
				line-height: 25px;
			}
			@media (max-width: 767px){
				font-size: 17px;
				line-height: 23px;
				margin-top: 15px;
			}
			@media (max-width: 512px){
				font-size: 16px;
			}
			@media (max-width: 400px){
				font-size: 15px;
				line-height: 20px;
			}
		}
		@nest & p{
			font-size: 15px;
			margin: 11px 0 0;
			line-height: 24px;
			@media (max-width: 991px){
				font-size: 14px;
				margin: 10px 0 0;
				line-height: 20px;
			}
			@media (max-width: 512px){
				font-size: 12px;
				line-height: normal;
			}
		}
	}
	@nest &--sm &{
		@nest &__content{
			max-width: 340px;
			padding: 0 10px 21px 33px;
			@media (max-width: 1199px){
				padding: 0 10px 25px 25px;
			}
			@media (max-width: 991px){
				padding: 0 10px 15px 20px;
			}
		}
	}
	@nest &--sm&{
		@nest &:after{
			background: color(#0b0b17 alpha(62%));
		}
		@nest & p{
			margin: 28px 0 0;
			@media (max-width: 1199px){
				font-size: 14px;
				margin: 15px 0 0;
				line-height: 22px;
			}
			@media (max-width: 991px){
				margin: 10px 0 0;
				line-height: 20px;
			}
			@media (max-width: 512px){
				font-size: 12px;
				line-height: normal;
			}
		}
	}
	@nest &__content{
		position: absolute;
		content: '';
		left: 0;
		right: 0;
		bottom: 0;
		width: 100%;
		z-index: 20;
	}
	@nest & img{
		width: 100%;
		height: 100%;
		object-fit: cover;
	}
	@nest & span{
		font-size: 14px;
		color: #fff;
		display: block;
		letter-spacing: .9px;
		position: relative;
		text-transform: uppercase;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			bottom: -8px;
			width: 44px;
			height: 1px;
			background: color(#fff alpha(46%));
		}
		@media (max-width: 991px){
			font-size: 12px;
		}
		@media (max-width: 512px){
			font-size: 10px;
		}
	}
	@nest & h2{
		color: #fff;
		font-weight: 800;
		@media (max-width: 512px){
			max-height: 46px;
			overflow: hidden;
		}
	}
	@nest & p{
		color: #fff;
		@media (max-width: 512px){
			max-height: 30px;
			overflow: hidden;
		}
		@media (max-width: 400px){
			display: none;
		}
	}
}