.share{
	font-size: 0;
	margin: 40px 0 0;
	@media (max-width: 512px){
		display: inline-block;
	}
	@nest &--right{
		text-align: right;
	}
	@nest &--indent-top{
		margin-top: 21px;
	}
	@nest &__link{
		width: 54px;
		height: 49px;
		position: relative;
		display: inline-block;
		text-decoration: none;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@nest &--fb{
			background: #1e17a9;
		}
		@nest &--tw{
			background: #3291ea;
		}
		@nest &--vk{
			background: #23577f;
		}
		@nest &--add{
			border: 1px solid #d2d3d9;
			border-left: 0;
			transition: border 300ms;
			@nest &:hover{
				opacity: 1;
				border-color: #767780;
				@nest &:before, &:after{
					background: #0b0b17;
				}
			}
			@nest &:before, &:after{
				position: absolute;
				content: '';
				top: 50%;
				left: 50%;
				background: #a7aab7;
				transition: background 300ms;
			}
			@nest &:before{
				height: 2px;
				width: 20px;
				margin-top: -1px;
				margin-left: -10px;
			}
			@nest &:after{
				height: 20px;
				width: 2px;
				margin-left: -1px;
				margin-top: -10px;
				transform: rotate(180deg);
			}
		}
		@nest & i{
			position: absolute;
			content: '';
			top: 50%;
			left: 50%;
			font-size: 24px;
			color: #fff;
			transform: translate(-50%, -50%);
		}
	}
}