.posts{
	@nest &__btn{
		font-size: 12px;
		color: #0b0b17;
		width: 199px;
		height: 46px;
		padding: 0;
		outline: none;
		display: block;
		z-index: 10;
		font-weight: 800;
		position: relative;
		line-height: normal;
		margin: 15px auto 0;
		letter-spacing: .8px;
		background: transparent;
		text-transform: uppercase;
		border: 1px solid #0b0b17;
		transition: color 300ms;
		@nest &:hover{
			color: #fff;
			@nest &:after{
				width: 100%;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			top: 0;
			bottom: 0;
			width: 0;
			height: 100%;
			z-index: -1;
			background: #0b0b17;
			transition: width 300ms, background 300ms;
		}
	}
}