.comment{
	background: #fff;
	margin-bottom: 13px;
	padding: 30px 55px 32px 30px;
	@media (max-width: 767px){
		padding: 20px;
	}
	@nest &--indent-bot{
		margin-bottom: 0;
	}
	@nest &--answer{
		padding-left: 77px;
		border-top: 3px solid #f6f6f6;
		@media (max-width: 767px){
			padding-left: 50px;
		}
	}
	@nest & img{
		float: left;
		@media (max-width: 512px){
			float: none;
		}
	}
	@nest &__content{
		padding: 0 0 0 70px;
		@media (max-width: 512px){
			padding-left: 0;
		}
	}
	@nest & span{
		font-size: 14px;
		color: #0b0b17;
		display: block;
		font-weight: 800;
		margin: 5px 0 6px;
		@media (max-width: 512px){
			margin-top: 8px;
		}
	}
	@nest & p{
		font-size: 14px;
		color: #333341;
		line-height: 24px;
		margin: 0 0 9px;
		@media (max-width: 512px){
			font-size: 13px;
			line-height: 22px;
		}
	}
	@nest & time{
		font-size: 14px;
		color: #79797d;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 512px){
			font-size: 12px;
		}
	}
	@nest & button{
		padding: 0;
		border: 0;
		outline: none;
		font-size: 14px;
		color: #002acc;
		margin-left: 25px;
		letter-spacing: .6px;
		display: inline-block;
		vertical-align: middle;
		background: transparent;
		text-transform: uppercase;
		transition: color 300ms;
		@nest &:hover{
			color: #001f96;
		}
		@media (max-width: 512px){
			font-size: 12px;
		}
	}
}