.article-bot{
	margin: 30px 0 0;
	border-top: 1px solid #e1e1e1;
	@nest & h6{
		font-size: 12px;
		color: #79797d;
		font-weight: 800;
		margin: 30px 0 7px;
		letter-spacing: .8px;
		text-transform: uppercase;
	}
	@nest & span{
		font-size: 14px;
		color: #0b0b17;
		display: block;
		@nest & a{
			color: #002acc;
			text-decoration: none;
			@nest &:hover{
				text-decoration: underline;
			}
		}
	}
}