.pagination{
	width: 100%;
	display: table;
	margin: 50px 0 70px;
	border: 1px solid #e1e1e1;
	@nest & a{
		width: 50%;
		padding: 50px 95px 30px;
		display: table-cell;
		position: relative;
		text-decoration: none;
		transition: background 300ms;
		@nest &:hover{
			background: color(#000 alpha(10%));
		}
		@media (max-width: 1199px){
			padding: 30px 15px 30px 60px;
		}
		@media (max-width: 767px){
			padding-left: 50px;
		}
		@media (max-width: 512px){
			display: block;
			width: 100%;
		}
		@nest &:first-child{
			border-right: 1px solid #e1e1e1;
			@media (max-width: 512px){
				border-right: 0;
				border-bottom: 1px solid #e1e1e1;
			}
			@nest & i{
				left: 50px;
				@media (max-width: 1199px){
					left: 20px;
				}
				@media (max-width: 991px){
					top: 50%;
					transform: translateY(-50%);
				}
				@media (max-width: 767px){
					left: 15px;
				}
			}
		}
		@nest &:last-child{
			text-align: right;
			@media (max-width: 1199px){
				padding-left: 15px;
				padding-right: 60px;
			}
			@media (max-width: 767px){
				padding-right: 50px;
			}
			@nest & i{
				right: 50px;
				@media (max-width: 1199px){
					right: 20px;
				}
				@media (max-width: 991px){
					top: 50%;
					transform: translateY(-50%);
				}
				@media (max-width: 767px){
					right: 15px;
				}
			}
		}
		@nest & i{
			position: absolute;
			content: '';
			top: 50px;
			font-size: 45px;
			color: #0b0b17;
		}
		@nest & span{
			font-size: 12px;
			color: #79797d;
			display: block;
			font-style: normal;
			letter-spacing: .8px;
			text-transform: uppercase;
		}
		@nest & p{
			font-size: 18px;
			color: #0b0b17;
			display: block;
			font-weight: 800;
			margin: 10px 0 0;
			line-height: 23px;
		}
	}
}