.article-author{
	margin: 36px 0 0;
	text-align: left;
	display: inline-block;
	@nest &--indent-top{
		margin-top: 25px;
	}
	@nest & img{
		width: 56px;
		height: 56px;
		margin-left: 2px;
		border-radius: 50%;
		display: inline-block;
		vertical-align: middle;
	}
	@nest &__info{
		margin: 8px 5px 0 8px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 767px){
			margin: 0 0 0 5px;
		}
		@nest & span{
			display: block;
			font-size: 16px;
			color: #0b0b17;
		}
		@nest & time{
			display: block;
			font-size: 14px;
			color: #79797d;
			margin: 7px 0 0;
			letter-spacing: .8px;
		}
	}
	@nest &__items{
		line-height: 19px;
		display: inline-block;
		vertical-align: bottom;
		@media (max-width: 767px){
			line-height: 26px;
		}
		@nest & a{
			font-size: 12px;
			color: #79797d;
			margin: 0 14px 0 0;
			text-decoration: none;
			display: inline-block;
			vertical-align: middle;
			transition: color 300ms;
			@nest &:hover{
				color: #002acc;
			}
			@media (max-width: 767px){
				margin: 0;
			}
			@media (max-width: 512px){
				margin: 0 3px;
			}
		}
	}
}