.page-title{
	min-height: 677px;
	position: relative;
	background: resolve('title.jpg') no-repeat center;
	-webkit-background-size: cover;
	background-size: cover;
	@media (max-width: 991px){
		min-height: 450px;
	}
	@media (max-width: 767px){
		min-height: 300px;
	}
	@nest &__content{
		position: absolute;
		content: '';
		top: 35%;
		left: 0;
		right: 0;
		width: 100%;
		text-align: center;
		@media (max-width: 991px){
			top: 50%;
			transform: translateY(-50%);
		}
	}
	@nest & span{
		font-size: 14px;
		color: #fff;
		position: relative;
		display: inline-block;
		margin-bottom: 40px;
		text-transform: uppercase;
		@media (max-width: 991px){
			margin-bottom: 30px;
		}
		@media (max-width: 767px){
			font-size: 13px;
			margin-bottom: 20px;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 50%;
			bottom: -7px;
			height: 1px;
			width: 44px;
			background: #fff;
			transform: translateX(-50%);
		}
	}
	@nest & h1{
		font-size: 46px;
		color: #fff;
		margin: 0;
		font-weight: 800;
		@media (max-width: 767px){
			font-size: 27px;
		}
	}
}