.footer-nav{
	float: right;
	@media (max-width: 767px){
		float: none;
		margin-bottom: 15px;
	}
	@nest &__list{
		padding: 0;
		margin: 0;
		list-style: none;
		@media (max-width: 512px){
			text-align: left;
		}
	}
	@nest &__item{
		margin-left: 25px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 767px){
			margin: 0 10px;
		}
		@media (max-width: 512px){
			margin: 0 10px 0 0;
		}
	}
	@nest &__link{
		font-size: 11px;
		color: #74798b;
		letter-spacing: .7px;
		display: inline-block;
		text-decoration: none;
		position: relative;
		text-transform: uppercase;
		transition: color 300ms;
		@nest &:hover{
			color: #fff;
		}
	}
}