.post-block{
	display: block;
	font-size: 0;
	text-decoration: none;
	margin-bottom: 33px;
	@nest &--sm &{
		@nest &__content{
			padding-bottom: 42px;
			@media (max-width: 1199px){
				padding: 12px 25px 15px;
			}
			@media (max-width: 767px){
				padding: 12px 15px 10px;
			}
		}
	}
	@nest &--sm&{
		@nest & img{
			@media (max-width: 512px){
				height: 180px;
				object-fit: cover;
			}
		}
		@nest & h3{
			margin-top: 7px;
			line-height: 34px;
			letter-spacing: 0px;
			@media (max-width: 1199px){
				font-size: 21px;
				line-height: 27px;
			}
			@media (max-width: 767px){
				font-size: 17px;
				line-height: 23px;
				margin-top: 10px;
			}
		}
		@nest & time{
			@media (max-width: 767px){
				font-size: 14px;
			}
		}
		@nest & p{
			@media (max-width: 767px){
				margin-top: 10px;
			}
		}
	}
	@nest &--no-image &{
		@nest &__content{
			padding-bottom: 22px;
		}
	}
	@nest &__content{
		background: #fff;
		padding: 12px 40px 18px;
		@media (max-width: 1199px){
			padding: 12px 30px 18px;
		}
		@media (max-width: 512px){
			padding: 12px 20px 15px;
		}
	}
	@nest &--sm-img &{
		@nest &__content{
			padding-bottom: 32px;
			@media (max-width: 767px){
				padding-bottom: 10px;
			}
		}
	}
	@nest &--sm-img&{
		max-width: 350px;
		margin: 0 auto 40px;
		@nest & img{
			height: 139px;
			object-fit: cover;
		}
	}
	@nest & img{
		width: 100%;
	}
	@nest & span{
		font-size: 14px;
		color: #002acc;
		padding-bottom: 6px;
		position: relative;
		letter-spacing: .5px;
		display: inline-block;
		text-decoration: none;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			bottom: 0;
			height: 1px;
			width: 44px;
			background: #002acc;
			transition: width 300ms;
		}
	}
	@nest & h3{
		font-size: 25px;
		color: #0b0b17;
		font-weight: 800;
		margin: 15px 0 0;
		line-height: 33px;
		letter-spacing: .3px;
		@media (max-width: 767px){
			font-size: 20px;
			line-height: 27px;
		}
		@nest & br{
			@media (max-width: 1199px){
				display: none;
			}
		}
	}
	@nest & time{
		font-size: 16px;
		color: #0b0b17;
		display: block;
		margin: 14px 0 0;
		letter-spacing: .2px;
	}
	@nest & p{
		font-size: 15px;
		color: #78787d;
		margin: 13px 0 0;
		line-height: 23px;
		@media (max-width: 767px){
			font-size: 14px;
		}
		@nest & br{
			@media (max-width: 1199px){
				display: none;
			}
		}
	}
}