.footer{
	padding: 25px 0 29px;
	background: #0b0b17;
	@media (max-width: 991px){
		text-align: center;
	}
	@nest &__top{
		margin-bottom: 22px;
		padding-bottom: 65px;
		border-bottom: 1px solid #363744;
		@media (max-width: 991px){
			padding-bottom: 30px;
		}
		@nest & span{
			display: block;
			font-size: 14px;
			color: #a7aab7;
			margin: 31px 0 0;
		}
	}
	@nest &__logo{
		position: relative;
		left: -17px;
		display: inline-block;
		text-decoration: none;
		transition: opacity 300ms;
		@nest &:hover{
			opacity: .7;
		}
		@media (max-width: 991px){
			left: 0;
		}
	}
	@nest & h5{
		color: #fff;
		font-size: 18px;
		font-weight: 800;
		margin: 27px 0 0;
		text-transform: uppercase;
		@media (max-width: 991px){
			margin: 50px 0 0;
		}
	}
	@nest &__form{
		clear: fix;
		max-width: 295px;
		margin: 23px 0 0;
		position: relative;
		text-align: center;
		@media (max-width: 991px){
			margin-left: auto;
			margin-right: auto;
		}
		@nest & i{
			position: absolute;
			content: '';
			left: 19px;
			top: 14px;
			width: 16px;
			height: 13px;
			color: #79797d;
		}
	}
	@nest &__input{
		width: 192px;
		float: left;
		font-size: 15px;
		color: #fff;
		height: 46px;
		outline: none;
		padding-left: 45px;
		padding-right: 5px;
		border: 1px solid #56565f;
		border-right: 0;
		background: transparent;
		@nest &::placeholder{
			color: #ababb0;
		}
	}
	@nest &__btn{
		font-size: 12px;
		color: #fff;
		border: 0;
		padding: 0;
		width: 103px;
		float: left;
		outline: none;
		height: 46px;
		z-index: 10;
		line-height: 47px;
		letter-spacing: .8px;
		font-weight: 800;
		position: relative;
		overflow: hidden;
		background-color: transparent;
		text-transform: uppercase;
		transition: background 300ms;
		@nest &:hover{
			background: transparent;
			@nest &:before{
				height: 0;
			}
			@nest &:after{
				height: 100%;
			}
		}
		@nest &:before{
			position: absolute;
			content: '';
			top: 0;
			left: 0;
			right: 0;
			width: 100%;
			z-index: -1;
			height: 100%;
			background: #002acc;
			transition: height 300ms;
		}
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			right: 0;
			bottom: 0;
			width: 100%;
			height: 0;
			z-index: -2;
			border: 1px solid #56565f;
			transition: height 300ms;
		}
	}
	@nest &__social{
		margin: 31px 0 0;
		@nest &-link{
			font-size: 24px;
			margin-right: 31px;
			display: inline-block;
			vertical-align: middle;
			text-decoration: none;
			transition: color 300ms;
			color: color(#fff alpha(77%));
			@nest &:hover{
				color: #fff;
			}
			@media (max-width: 991px){
				margin: 0 15px;
			}
		}
	}
	@nest &__bottom{
		text-align: left;
		@media (max-width: 767px){
			text-align: center;
		}
		@nest & span{
			font-size: 12px;
			color: #74798b;
			margin-right: 10px;
			display: inline-block;
			vertical-align: middle;
		}
	}
}