.article{
	@nest &--indent-top{
		margin-top: 51px;
	}
	@nest &__content{
		padding-right: 90px;
	}
	@nest & h2{
		font-size: 35px;
		color: #333341;
		font-weight: 800;
		line-height: 43px;
		margin: 35px 0;
		@media (max-width: 767px){
			font-size: 25px;
			line-height: 35px;
			margin: 25px 0;
		}
		@media (max-width: 512px){
			font-size: 22px;
			line-height: 25px;
			margin: 20px 0;
		}
	}
	@nest & p{
		font-size: 18px;
		color: #333341;
		margin: 15px 0;
		line-height: 30px;
		@media (max-width: 767px){
			font-size: 16px;
			line-height: 25px;
			margin: 10px 0;
		}
		@media (max-width: 512px){
			font-size: 15px;
			line-height: 24px;
		}
	}
	@nest & blockquote{
		font-size: 30px;
		color: #171838;
		font-weight: 800;
		line-height: 39px;
		margin: 40px 0 37px;
		position: relative;
		@media (max-width: 991px){
			padding: 0 70px;
			font-size: 22px;
			line-height: 30px;
			margin: 25px 0;
		}
		@media (max-width: 767px){
			padding: 0 50px;
			font-size: 20px;
			line-height: 27px;
		}
		@media (max-width: 512px){
			padding: 0 30px;
			font-size: 16px;
			line-height: 23px;
		}
		@nest &:before{
			position: absolute;
			content: '';
			top: 28%;
			left: -90px;
			background: resolve('qoute-left.svg');
			width: width('qoute-left.svg');
			height: height('qoute-left.svg');
			@media (max-width: 991px){
				left: 0;
			}
			@media (max-width: 767px){
				width: 30px;
				background-repeat: no-repeat;
				background-position: center;
				-webkit-background-size: contain;
				background-size: contain;
			}
			@media (max-width: 512px){
				width: 20px;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			top: 28%;
			right: -90px;
			background: resolve('qoute-right.svg');
			width: width('qoute-right.svg');
			height: height('qoute-right.svg');
			@media (max-width: 991px){
				right: 0;
			}
			@media (max-width: 767px){
				width: 30px;
				background-repeat: no-repeat;
				background-position: center;
				-webkit-background-size: contain;
				background-size: contain;
			}
			@media (max-width: 512px){
				width: 20px;
			}
		}
	}
	@nest & cite{
		font-size: 15px;
		color: #002acc;
		display: block;
		font-style: normal;
		text-align: right;
		margin-bottom: 27px;
		letter-spacing: 1.2px;
		text-transform: uppercase;
		@media (max-width: 991px){
			font-size: 13px;
			margin-bottom: 20px;
		}
		@media (max-width: 512px){
			font-size: 10px;
			font-weight: 800;
		}
	}
	@nest & > span{
		font-size: 14px;
		color: #002acc;
		display: inline-block;
		position: relative;
		letter-spacing: .5px;
		@nest &:after{
			position: absolute;
			content: '';
			left: 0;
			bottom: -6px;
			height: 1px;
			width: 44px;
			background: #002acc;
		}
	}
	@nest & h1{
		font-size: 62px;
		color: #0b0b17;
		font-weight: 800;
		margin: 22px 0 0;
		line-height: 65px;
		letter-spacing: -1.3px;
	}
}