.sidebar-news{
	clear: fix;
	margin: 70px 0 40px;
	padding: 30px 35px 20px;
	border: 4px solid #e1e1e1;
	@nest & h4{
		font-size: 15px;
		color: #0b0b17;
		margin: 0 0 35px;
		letter-spacing: 1px;
		text-transform: uppercase;
	}
	@nest &__item{
		display: block;
		margin-bottom: 25px;
		text-decoration: none;
		@nest & span{
			font-size: 12px;
			color: #79797d;
			display: block;
			margin: 10px 0 0;
			letter-spacing: .8px;
			text-transform: uppercase;
		}
		@nest & p{
			margin: 5px 0 0;
			font-size: 22px;
			color: #0b0b17;
			font-weight: 800;
			@media (max-width: 1199px){
				font-size: 19px;
				line-height: 22px;
			}
		}
	}
	@nest &__more{
		float: right;
		font-size: 12px;
		color: #002acc;
		padding-right: 15px;
		text-decoration: none;
		position: relative;
		text-transform: uppercase;
		@nest &:hover{
			@nest &:after{
				right: -5px;
			}
		}
		@nest &:after{
			position: absolute;
			content: '';
			right: 0;
			top: 50%;
			transition: right 300ms;
			border: 4px solid transparent;
			border-left: 4px solid #002acc;
			transform: translateY(-50%);
		}
	}
}