.sidebar-nav{
	padding-bottom: 49px;
	@nest &__list{
		margin: 35px 0 0;
		list-style: none;
		padding: 0 0 0 2px;
		display: inline-block;
		@nest & a{
			display: block;
			font-size: 20px;
			color: #002acc;
			font-weight: 800;
			margin-bottom: 12px;
			letter-spacing: .45px;
			text-decoration: none;
			position: relative;
			@nest &:hover{
				@nest &:after{
					width: 100%;
				}
			}
			@nest &:after{
				position: absolute;
				content: '';
				left: 0;
				bottom: 0;
				width: 0;
				height: 2px;
				background: #002acc;
				transition: width 200ms, background 200ms;
			}
		}
	}
}