.banner{
	height: 576px;
	display: block;
	background: #e7e7e7;
	position: relative;
	@nest &__content{
		position: absolute;
		content: '';
		left: 0;
		right: 0;
		top: 40%;
		text-align: center;
		@nest & span{
			display: block;
			font-size: 36px;
			color: #002acc;
			font-weight: 800;
		}
		@nest & i{
			display: block;
			font-size: 19px;
			color: #79797d;
			margin: 11px 0 0;
			font-style: normal;
		}
	}
}