.nav-list{
	padding: 0;
	margin: 0;
	list-style: none;
	@nest &__item{
		margin: 0 13px;
		display: inline-block;
		vertical-align: middle;
		@media (max-width: 1199px){
			margin: 0 8px;
		}
		@media (max-width: 991px){
			display: block;
			margin: 0 0 10px;
			text-align: center;
		}
	}
	@nest &__link{
		font-size: 14px;
		color: #0b0b17;
		font-weight: 800;
		letter-spacing: 1.3px;
		text-decoration: none;
		text-transform: uppercase;
		transition: color 300ms;
		@nest &:hover{
			color: #ff2a00;
		}
		@media (max-width: 1199px){
			font-size: 13px;
		}
	}
}