.sidebar{
	@media (max-width: 991px){
		display: none;
	}
	@nest &__item{
		margin: 37px 0 0;
		border: 4px solid #e1e1e1;
	}
	@nest &__block{
		padding: 37px 45px 0;
		border-bottom: 4px solid #e1e1e1;
		@media (max-width: 1199px){
			padding: 37px 25px 0;
		}
		@nest &:last-child{
			border-bottom: 0;
		}
		@nest & span{
			display: block;
			font-size: 15px;
			color: #0b0b17;
			font-weight: 800;
			letter-spacing: 1.4px;
			text-transform: uppercase;
		}
	}
	@nest &__facebook{
		padding-bottom: 50px;
		@nest &-link{
			display: block;
			font-size: 0;
			margin-top: 13px;
			text-align: center;
			text-decoration: none;
		}
	}
	@nest &__subscription{
		padding-bottom: 62px;
		position: relative;
		overflow: hidden;
		@nest &:after{
			position: absolute;
			content: '';
			left: -32px;
			bottom: -24px;
			background: resolve('msg.svg');
			width: width('msg.svg');
			height: height('msg.svg');
		}
		@nest & h5{
			font-size: 23px;
			color: #0b0b17;
			line-height: 33px;
			margin: 31px 0 27px;
			letter-spacing: .3px;
		}
		@nest &-form{
			position: relative;
			text-align: center;
			@nest & i{
				position: absolute;
				content: '';
				left: 19px;
				top: 14px;
				width: 16px;
				height: 13px;
				color: #0b0b17;
			}
		}
		@nest &-input{
			width: 100%;
			font-size: 15px;
			color: #000;
			height: 46px;
			outline: none;
			padding-left: 45px;
			margin-bottom: 40px;
			border: 1px solid #d9d9d9;
			background: transparent;
			@nest &::placeholder{
				color: #ababb0;
			}
		}
		@nest &-btn{
			font-size: 12px;
			border: 0;
			padding: 0;
			outline: none;
			color: #002acc;
			font-weight: 800;
			letter-spacing: .75px;
			position: relative;
			background: transparent;
			text-transform: uppercase;
			@nest &:hover{
				@nest &:before, &:after{
					width: 100%;
				}
			}
			@nest &:before{
				position: absolute;
				content: '';
				left: 0;
				bottom: 0;
				height: 1px;
				width: 0;
				background: #002acc;
				transition: width 300ms, background 300ms;
			}
			@nest &:after{
				position: absolute;
				content: '';
				right: 0;
				bottom: 0;
				height: 1px;
				width: 0;
				background: #002acc;
				transition: width 300ms, background 300ms;
			}
		}
	}
}